#ifndef _CILD_H_
#define _CILD_H_

#include <unordered_map>
#include <Common/FiltersChain.h>
#include <Common/Buffer.h>

/*! \file */

/** \brief Class to be used as Key in the hash table used by CILD
*/
class CILD_Key
{
public:
	int distance;      ///< Distance to the center of the head, in millimeters 
	int azimuth;       ///< Azimuth angle of interaural coordinates, in degrees

	CILD_Key() :CILD_Key{ 0,0 } {}

	CILD_Key(int _distance, int _azimuth) :distance{ _distance }, azimuth{ _azimuth } {}

	bool operator==(const CILD_Key& key) const
	{
		return (this->azimuth == key.azimuth && this->distance == key.distance);
	}
};

namespace std
{
	template<>
	struct hash<CILD_Key>
	{
		// adapted from http://en.cppreference.com/w/cpp/utility/hash
		size_t operator()(const CILD_Key & key) const
		{
			size_t h1 = std::hash<int>()(key.distance);
			size_t h2 = std::hash<int>()(key.azimuth);
			return h1 ^ (h2 << 1);  // exclusive or of hash functions for each int.
		}
	};
}

/** \brief Template class to hold the coefficients of a set of biquad filters.
*/
template <int NUMBER_OF_BIQUAD_FILTERS>
class CILD_BiquadFilterCoefs
{
public:
	float coefs[5 * NUMBER_OF_BIQUAD_FILTERS];   /**< Holds the coefficients of one or more biquad filters. 
												    Each biquad filter has 5 coefficients.
												    Format: f1_b0, f1_b1, f1_b2, f1_a1, f1_a2, f2_b0, f2_b1, f2_b2, f2_a1, f2_a2, ...
												        where fx means filter xth     */
	                                              
};

/** \brief Type definition to work with a set of coefficients for two biquad filters
*/
typedef CILD_BiquadFilterCoefs<2> CILD_TwoBiquadFilterCoefs;

/** \brief Hash table that contains a set of coefficients for two biquads filters that are indexed through a pair of distance
 and azimuth values (interaural coordinates). */
typedef std::unordered_map<CILD_Key, CILD_TwoBiquadFilterCoefs> ILD_HashTable;


/** \details Models the effect of distance when the sound source is close to the listener
*/
class CILD
{
// METHODS
public:

	/** \brief Default constructor.
	*	\details Leaves ILD Table empty. Use SetILD_HashTable to load. 
	*/
	CILD(); 

	/** \brief Set the hash table for computing ILD
	*	\param [in] _ILD_Data data for hash table
	*/
	static void SetILD_HashTable(ILD_HashTable && _ILD_Data) { ILD_Data = _ILD_Data;  }     

	/** \brief Get the internal hash table. 
	*	\retval ILD_Data data from the hash table
	*/
	const ILD_HashTable & GetILD_HashTable() { return ILD_Data;  }                         

	/** \brief Applies ILD to all audio samples in buffer
	*	\param [in,out] buffer input and output buffer
	*	\param [in] distance_m distance at which audio source is located, in meters
	*	\param [in] azimuth azimuth angle at which audio source is located, in degrees
	*	\pre buffer cannot be empty
	*	\pre distance must not be zero
	*	\pre azimuth must be between -90 and 90
	*   \throws May throw exceptions and errors to debugger
	*/
	void Process( CStereoBuffer<float> & buffer, float distance_m, float azimuth );  

// PRIVATE METHODS:
private:


// ATTRIBUTES
private:
	static ILD_HashTable ILD_Data;  // Global pointer to class data. FIXME: This currently allows for only one application-wide ILD table
	static int Default_ILD_AzimuthStep;
	static int Default_ILD_DistanceStep; 
	CFiltersChain FiltersLeft, FiltersRight;
};

#endif
