#ifndef _CLISTENER_H_
#define _CLISTENER_H_

#include <BinauralSpatializer/HRTF.h>
#include <Common/Transform.h>
#include <Common/Magnitudes.h>
#include <Common/Buffer.h>
#include <vector>
#include <memory>


namespace Binaural {

class CCore;

/** \details Class for centralization of several funtionalities of the binaural spatializer, such as handling sound sources, audio state, listener and environment.
*/
class CListener
{
    // METHODS
public:
    
	/** \brief Constructor with parameters should not be called directly (use CreateListener in \link Core \endLink instead)
	*	\param [in] _listenerHeadRadius listener head radius for ITD customization, in meters
    *	\param [in] _ownerCore audio state configuration (See \link AudioState \endlink)
	*/
	CListener(CCore* _ownerCore, float _listenerHeadRadius=0.0875f);

	/** \brief Set listener position and orientation
	*	\param [in] _listenerTransform new listener position and orientation
	*	\note This method is implemented in \link CoreState \endlink
	*/
	void SetListenerTransform(CTransform _listenerTransform);							

	/** \brief Get listener position and orientation
	*	\retval transform current listener position and orientation
	*	\note This method is implemented in \link CoreState \endlink
	*/
	const CTransform GetListenerTransform() const;										

	/** \brief Set HRTF of listener 
	*	\param [in] _listenerHRTF new listener HRTF
	*	\note This method is implemented in \link CoreState \endlink
	*/
	void LoadHRTF(CHRTF && _listenerHRTF);												

	/** \brief Get HRTF of listener
	*	\retval HRTF current listener HRTF
	*	\note This method is implemented in \link CoreState \endlink
	*/
	const CHRTF& GetHRTF() const;														

	/** \brief Set head radius of listener
	*	\param [in] _listenerHeadRadius new listener head radius, in meters
	*	\note This method is implemented in \link CoreState \endlink
	*/
	void SetHeadRadius(float _listenerHeadRadius);										

	/** \brief Switch on/off customized ITD
	*	\details Listener head radius is used only if ITD customization is enabled
	*	\param [in] enable switch for ITD customization
	*	\note This method is implemented in \link CoreState \endlink
	*/
	void SetCustomizedITD(bool enable);													

	/** \brief Get head radius of listener
	*	\retval radius current listener head radius, in meters
	*	\note This method is implemented in \link CoreState \endlink
	*/
	float GetHeadRadius() const;													

	/** \brief Get position and orientation of listener left ear
	*	\retval transform current listener left ear position and orientation
	*	\note This method is implemented in \link CoreState \endlink
	*/
	const CTransform GetListenerLeftEarTransform() const;								

	/** \brief Get position and orientation of listener right ear
	*	\retval transform current listener right ear position and orientation
	*	\note This method is implemented in \link CoreState \endlink
	*/
	const CTransform GetListenerRightEarTransform() const;								


// ATTRIBUTES
private:

	// HRTF of listener
    CHRTF listenerHRTF;
    
    // Head radius of listener
	float listenerHeadRadius;
    
    // Transform matrix (position and orientation) of listener
	CTransform listenerTransform;

	// owner Core
    CCore* ownerCore;
};
    
}

#endif