#ifndef _CSINGLESOURCEDSP_H_
#define _CSINGLESOURCEDSP_H_

#include <Common/Buffer.h>
#include <Common/Transform.h>
#include <BinauralSpatializer/Convolver.h>
#include <BinauralSpatializer/Core.h>
#include <BinauralSpatializer/Listener.h>
#include <Common/DistanceAttenuator.h>


namespace Binaural {

class CCore;

/** \details This class manages the spatialization of a single source
*/
class CSingleSourceDSP
{
// PUBLIC TYPES
public:

	/** \brief Type definition to allow switching on/off different modules that belong to the processes in CSingleSourceDSP 
	*/
	struct ModulesEnabler
	{
	public:
		bool doFarDistanceLPF;        ///< Enables/Disables the low pass filtering that is applied at far distances
		bool doDistanceAttenuation;   ///< Enables/Disables the attenuation that depends on the distance to the listener 
		bool doILD;                   ///< Enables/Disables the ILD (Interaural Level Difference) processing
		bool doHRTF;                  ///< Enables/Disables the HRTF convolution

		ModulesEnabler() : doFarDistanceLPF(true), doDistanceAttenuation(true), doILD(true), doHRTF(true) {}
	};

// PUBLIC METHODS
public:

//Constructors

	/** \brief Constructor with parameters
	*	\Details links the source to the binaural core
	*	\param [in] _ownerCore pointer to the binaural core
	*	\exception May throw exceptions and errors to debugger
	*/
	CSingleSourceDSP(CCore* _ownerCore);

	/** \brief Default constructor
	*	\details Does nothing
	*/
	CSingleSourceDSP();

// Basic in/out methods

	/** \brief Update internal buffer
	*	\details This must be called before calling to \link ProcessAnechoic \endlink or \link ProcessVirtualAmbisonicReverb \endlink
	*	\param [in] buffer reference to new buffer content
	*/
	void UpdateBuffer(CMonoBuffer<float> & buffer);					

	/** \brief Get internal buffer
	*	\retval buffer internal buffer content
	*	\exception May throw exceptions and errors to debugger
	*/
	const CMonoBuffer<float> GetBuffer() const;						

	/** \brief Process data from internal buffer to generate anechoic spatialization (direct path)
	*	\param [out] outBuffer output stereo buffer with spatialized audio
	*	\pre Internal buffer must be updated (See \link UpdateBuffer \endlink)
	*	\exception May throw exceptions and errors to debugger
	*/
	void ProcessAnechoic(const CListener& listener, CStereoBuffer<float> & outBuffer);

	/** \brief Process data from input buffer to generate anechoic spatialization (direct path)	
	*	\param [in] inBuffer input buffer with anechoic audio
	*	\param [out] outBuffer output stereo buffer with spatialized audio	
	*	\pre Internal buffer must be updated (See \link UpdateBuffer \endlink)
	*	\exception May throw exceptions and errors to debugger
	*/
	void ProcessAnechoic(const CListener & listener, const CMonoBuffer<float> & inBuffer, CStereoBuffer<float> & outBuffer);
																	
// Get/Set methods

	/** \brief Move source (position and orientation)
	*	\param [in] _sourceTransform new position and orientation of source
	*/
	void SetSourceTransform(CTransform _sourceTransform);		    
	
	/** \brief Get source transform (position and orientation)
	*	\retval transform current position and orientation of source
	*/
	const CTransform & GetSourceTransform() const;				    


	/** \brief Set HRTF convolution method
	*	\param [in] _interpolate HRTF interpolation method (true: perform convolution in frequency domain, false: perform convolution in time domain)
	*/
	void SetFrequencyConvolution(bool _bFreqConvolution);

	/** \brief Set HRTF interpolation method
	*	\param [in] _interpolate HRTF interpolation method (true: Run-time interpolation ON, false: run-time interpolation OFF)
	*	\sa GetInterpolated_HRIR
	*/
	void SetInterpolation(bool _bInterpolate);

// Distance Model Methods

	/** \brief Get the cutoff frequency of the long-distance filter for a given distance. 
	*	\details The long-distance filter is a low-pass filter which cutoff frequency depends on distance
	*	\param [in] distance distance for checking long-distance filter cutoff
	*	\retval cutoff cutoff frequency of long-distance filter for given distance
	*/
	float GetCutOffFreqForFarDistances(float distance);                 
	                                                                    
	/** \brief Get the attenuation in anechoic process for a given distance 	
	*	\param [in] distance distance for checking attenuation
	*	\retval gain attenuation, as gain (typically, between 0 and 1)
	*	\exception May throw exceptions and errors to debugger
	*/
	float GetAnechoicDistanceAttenuation(float distance) const;			

	/** \brief Get the attenuation in reverb process for a given distance
	*	\param [in] distance distance for checking attenuation
	*	\retval gain attenuation, as gain (typically, between 0 and 1)
	*	\exception May throw exceptions and errors to debugger
	*/
	float GetReverbDistanceAttenuation(float distance) const;			

	/** \brief Initialize anechoic convolvers 
	*	\details Allocates memory for buffers used internally for convolution
	*	\param [in] bufferSize size of buffers used for convolution. 
	*	\pre bufferSize must agree with buffer size defined in binaural core (See \link SetAudioState \endlink and \link CCore \endlink constructor)	
	*/
	void SetupAnechoicConvolvers(int bufferSize);						

// PRIVATE METHODS																	
private:		
	void PropagateSound(const CListener & listener, CStereoBuffer<float> & buffer, int bufferSize, int sampleRate);     // Applies sound propagation effects over a stereo buffer
	//void AddDelay(const CMonoBuffer<float> & input, int delaySize, delay_struct & signalDelay, CMonoBuffer<float> & output);

// ATTRIBUTES
public:
	ModulesEnabler modEnabler;                      ///< Modules can be switched on/off through this attribute (See \link ModulesEnabler \endlink)

private:	
	CTransform sourceTransform;						// Position and orientation of source
	const CCore* ownerCore;							// Reference to the core where information shared by all sources is stored (listener, room and audio state attributes)
	CConvolver leftConvolution;						// Time Convolution of the left channel
	CConvolver rightConvolution;					// Time Convolution of the rigth channel

	CDistanceAttenuator distAttenuator;             // Computes the attenuation due to distance
		
	bool bInterpolate;						// Vrble to activate the interpolation on run time (true) or deactivate (false)
	bool bFreqConvolution;					// Vrble to activate the convolution in frequency domain
		
	CMonoBuffer<float> internalBuffer;		// Buffer storage
	//Auxiliar objects to make the inverse fft of the channels
	Common::CFconvolver outputLeft;   	   // Frequency Convolution of the left channel
	Common::CFconvolver outputRight;	   // Frequency Convolution of the rigth channel
	
	//FIXME delete me
	bool firstTime;
};
    
}

#endif
