#ifndef _CCONVOLVER_H_
#define _CCONVOLVER_H_

#include <Common/Buffer.h>

/** \details This class implements the necessary algorithms to do the convolution between signal and HRIR. 
*/
class CConvolver
{
	
public:
	CConvolver();

	/** \brief Allocate memory space for the output buffer.
	*   \details Required to store previously computed convolutions
	*	\param bufferSize size of the convolution buffer.	
	*/	
	void SetBuffer(int bufferSize);
	
	/** \brief Set a new impulse response in the convolver
	*	\param _HRIR impulse response data. 
	*	\\param frameSize size of the input buffer signal
	*/
	void SetHRIR(const CMonoBuffer<float> & _HRIR, int frameSize);	

	/** \brief Do the convolution between the input and the stored HRIR. 
	*	\param [in] input input buffer. Must have the same size of the output buffer and HRIRs
	*	\param [out] _output output buffer
	*	\param [in] channelNumber number of the channel to be processed (1-left channel, 2-right channel).	
	*   \pre Before calling this method: 
	*   \pre (1) Allocate memory for buffer (\link SetBuffer \endlink)
	*   \pre (2) Set a new impulse response (\link SetHRIR \endlink)
	*   \throws May throw exceptions and errors to debugger
	*	\sa SetBuffer, SetHRIR
	*/
	void Process(const CMonoBuffer<float>& input, CStereoBuffer<float> & _output, int channelNumber);	

private:
	// ATTRIBUTES
	CMonoBuffer<float> h;		// h[n] Impulse response data.
	CMonoBuffer<float> buffer;	// This will store the necessary buffer to do the convolution. If size has to be equal to h[n] length.
	bool firstTime;				// TODO change something in order to delete this var

	// PRIVATE METHODS
	void SimpleTimeConvolver(float *input, float *h, float *output, float *buffer, 
							const unsigned inputLength, const unsigned hLength, 
							const unsigned bufferLength, int numberOfChannels, int channelNumber);	///< Simple convolver in time domain.
	void ShiftLeft(float *myarray, int size, int shiftBy);											///< Makes a left shift in the buffer of shiftBy positions
};

#endif