#ifndef PLATFORM_DEFINED
#if defined(__ANDROID_API__)
#define PLATFORM_ANDROID
#endif
#define PLATFORM_DEFINED
#endif

#include <BinauralSpatializer/Convolver.h>

	CConvolver::CConvolver() {
		firstTime = true;
	}

	// Set h[n] or HRIR and prepare a buffer with same length that HRIR. This allocates memory!!
	void CConvolver::SetHRIR(const CMonoBuffer<float> & _h, int frameSize)
	{
        h = _h;
		if (firstTime) {
			firstTime = false;
			if (_h.size() > frameSize) { SetBuffer(_h.size()); }
			else { SetBuffer(frameSize); }
		}	
	}//End SetHRIR
		
	 // Allocate memory space for the buffer required to store previously computed convolutions
	void CConvolver::SetBuffer(int bufferSize)
	{
		buffer.resize(bufferSize);
		buffer.assign(bufferSize, 0.0f);
	}
	
	// Do the convolution between the input and the HRIR stored. The output will have the same lenght that the input.
	void CConvolver::Process(const CMonoBuffer<float> & _input, CStereoBuffer<float> & _output, int channelNumber)
	{					
		ASSERT(h.size() > 0, RESULT_ERROR_NOTSET, "HRIR must be set for convolver", "");
		ASSERT(buffer.size() > 0, RESULT_ERROR_NOTSET, "Buffer must be set for convolver", "");
		ASSERT(h.size() == buffer.size(), RESULT_ERROR_BADSIZE, "Buffer and HRIR size must agree for time-domain convolver", "");

#ifndef PLATFORM_ANDROID

		//Call the convolver		
		SimpleTimeConvolver(const_cast<float*>(&_input[0]), 
							const_cast<float*>(&h[0]),
							const_cast<float*>(&_output[0]),
							const_cast<float*>(&buffer[0]),
							_input.size(), 
							h.size(), 
							buffer.size(), 
							2, 
							channelNumber);				//FIXME: the number of channels is hardcode at the moment

#else
		// QUICK AND DIRTY SOLUTION FOR RUNNING BENCHMARK
		float inputArray[512];
		float outputArray[512];
		float hArray[512];
		float bufferArray[512];
		for (int i = 0; i < 512; i++)
		{
			inputArray[i] = _input[i];
			outputArray[i] = 0.0f;
			hArray[i] = h[i];
			bufferArray[i] = buffer[i];
		}
		SimpleTimeConvolver(inputArray, hArray, outputArray, bufferArray,
			_input.size(),
			h.size(),
			buffer.size(),
			2,
			channelNumber);				//FIXME: the number of channels is hardcode at the moment
#endif
	}

////////////////////////////
//	Private Methods       //
////////////////////////////

	void CConvolver::SimpleTimeConvolver(float *input, float *h, float *output, float *buffer, const unsigned inputLength, const unsigned hLength, const unsigned bufferLength, int numberOfChannels, int channelNumber)
	{

		float yn; //accumulator
		int t, tt;

		//Convolution	
		for (int n = 0; n < inputLength; n++){			
			//y[n] = h[k] * x[n-k]
			yn = 0.f;
			for (int k = 0; k < hLength; k++){
				t = n - k;
				if (t > -1){
					//we are operating with the input
					if (t < inputLength)
						yn += h[k] * input[t];
				}
				else{
					//we are operating with the buffer
					tt = t + bufferLength;
					if (tt > -1)
						yn += h[k] * buffer[tt];
				}
			}
			//Save the result in the output. The ouput has the different channels interlaced.
			//TODO: Check that numberOfChannels >= channelNumber;
			if (channelNumber == 1)
			{
				//Channel 1 --> left -> [0, 2, 4, 6, 8]
				output[numberOfChannels*n] = yn;
			}
			else if (channelNumber == 2){
				//Channel 2 --> right -> [1, 3, 5, 7, 9]
				output[numberOfChannels*n + 1] = yn;
			}
			else
			{
				//TODO: Do for more channels
			}
		}
		//Update the Buffer
		//Shift left the buffer "inputLength" positions	
		ShiftLeft(buffer, bufferLength, inputLength);
		//Copy the input into the right part of the buffer 
		for (int i = bufferLength - inputLength; i < bufferLength; i++){
			buffer[i] = input[i - bufferLength + inputLength];
		}
	}//SimpleConvolveProcess

/////////////////////////
//	Auxiliar Algorithms  //
/////////////////////////

	void CConvolver::ShiftLeft(float *myarray, int size, int shiftBy)
	{
		if (shiftBy > size)
		{
			shiftBy = shiftBy - size;
		}

		if (size == 1)
		{
			//do nothing
		}
		else
		{
			for (int i = 0; i < size - shiftBy; i++)
			{
				myarray[i] = myarray[i + shiftBy];		//myarray[0] == myarray[2]			
			}
		}
	}//shiftLeft
