#ifndef _CCORE_H_
#define _CCORE_H_

#include <BinauralSpatializer/HRTF.h>
#include <Common/Transform.h>
#include <Common/AudioState.h>
#include <BinauralSpatializer/SingleSourceDSP.h>
#include <Common/Magnitudes.h>
#include <Common/AIR.h>
#include <Common/Buffer.h>
#include <Common/Fconvolver.h>
#include <vector>
#include <memory>


using namespace std;  //TODO: Try to avoid this

namespace Binaural {

    class CSingleSourceDSP;
    class CListener;
    class CEnvironment;
    

/** \details Class for centralization of several funtionalities of the binaural spatializer, such as handling sound sources, audio state, listener and environment.
*/
class CCore
{
// METHODS
public:

	/** \brief Constructor with parameters
	*	\param [in] _audioState audio state configuration (See \link AudioState \endlink)
	*	\param [in] _listenerHeadRadius listener head radius for ITD customization, in meters
	*/
	CCore(AudioState_Struct _audioState);

	/** \brief Default constructor 
	*	\details By default, sets sampling frequency to 44100Hz, buffer size to 512, and listenerHeadRadius to 0.0875 meters. 
	*/
	CCore();

//
// General purpose methods	
//

	/** \brief Get global audio state
	*	\retval audioState currently set audio state
	*/
	AudioState_Struct GetAudioState() const;

	/** \brief Set global audio state
	*	\param [in] _audioState audio state to set
	*/
	void SetAudioState(AudioState_Struct _audioState);									

	/** \brief Get global physical magnitudes
	*	\retval magnitudes currently set physical magnitudes
	*/
	CMagnitudes GetMagnitudes() const;													

	/** \brief Set global physical magnitudes
	*	\param [in] _magnitudes physical magnitudes to set
	*/
	void SetMagnitudes(CMagnitudes _magnitudes);	

//
// Listener methods
//

    /** \brief Creates a new listener
     *	\retval source shared pointer to newly created listener with empty HRTF.
     */
    shared_ptr<CListener> CreateListener(float listenerHeadRadius = 0.0875f);
    
    /** \brief Removes one listener udio source for spatialization
     *	\param [in] listener shared pointer to remove
     */
    void RemoveListener(shared_ptr<CListener> listener);

    
//
// Environment methods
//

    /** \brief Creates a new environment
     *	\retval source shared pointer to newly created environment with empty ABIR.
     */
    shared_ptr<CEnvironment> CreateEnvironment();
    
    /** \brief Removes one environment
     *	\param [in] environment shared pointer  to remove
     */
    void RemoveEnvironment(shared_ptr<CEnvironment> listener);

//
// Audio source methods
//
    
    /** \brief Creates a new audio source for spatialization
     *	\retval source shared pointer to newly created audio source
     *	\note This method is implemented in \link CoreState \endlink
     */
    shared_ptr<CSingleSourceDSP> CreateSingleSourceDSP();
    
    /** \brief Removes one audio source for spatialization
     *	\param [in] source shared pointer of audio source to remove
     *	\note This method is implemented in \link CoreState \endlink
     */
    void RemoveSingleSourceDSP(shared_ptr<CSingleSourceDSP> source);


// ATTRIBUTES
private:

	// Listener attributes
    vector<shared_ptr<CListener>> listeners;
	// Environment attributes
    vector<shared_ptr<CEnvironment>> environments;
 
	// General attributes
	AudioState_Struct audioState;					// Global audio state
	CMagnitudes magnitudes;					// Physical magnitudes

	// List of audio sources 
	vector<shared_ptr<CSingleSourceDSP>> audioSources;

	// Friend class State
    friend class CEnvironment;
};

}

#endif