#include <BinauralSpatializer/Listener.h>
#include <BinauralSpatializer/Core.h>


namespace Binaural {

//////////////////////////////////////////////
// CONSTRUCTOR/DESTRUCTOR

	CListener::CListener(CCore* _ownerCore, float _listenerHeadRadius)
    :ownerCore{_ownerCore},
     listenerHeadRadius{_listenerHeadRadius}
    {}


//////////////////////////////////////////////
// LISTENER METHODS

	// Set listener position and orientation
	void CListener::SetListenerTransform(CTransform _listenerTransform)
	{
		listenerTransform = _listenerTransform;
	}

//////////////////////////////////////////////

	// Get listener position and orientation
	const CTransform CListener::GetListenerTransform() const
	{
        return listenerTransform;
	}

//////////////////////////////////////////////

// Get position and orientation of listener left ear
	const CTransform CListener::GetListenerLeftEarTransform() const
	{
		CVector3 earLocalPosition = CVector3::ZERO;
		earLocalPosition.SetAxis(RIGHT_AXIS, -listenerHeadRadius);
		return listenerTransform.GetLocalTranslation(earLocalPosition);
	}

//////////////////////////////////////////////

	// Get position and orientation of listener right ear
	const CTransform CListener::GetListenerRightEarTransform() const
	{
		CVector3 earLocalPosition = CVector3::ZERO;
		earLocalPosition.SetAxis(RIGHT_AXIS, listenerHeadRadius);
		return listenerTransform.GetLocalTranslation(earLocalPosition);
	}

//////////////////////////////////////////////

	// Set HRTF for listener
	void CListener::LoadHRTF(CHRTF && _listenerHRTF)
	{
        // TODO: Check validity of _listenerHRTF?
        listenerHRTF = _listenerHRTF;
    }

	// Get HRTF for listener	
	const CHRTF& CListener::GetHRTF() const
	{
		return listenerHRTF;
	}
//////////////////////////////////////////////

	// Set head radius for listener (m)
	void CListener::SetHeadRadius(float _listenerHeadRadius)
	{
        listenerHeadRadius = _listenerHeadRadius;
        listenerHRTF.SetListenerHeadRadius(_listenerHeadRadius);
	}

	//Get listener head radious
	float CListener::GetHeadRadius() const{
        return listenerHeadRadius;
    }

	void CListener::SetCustomizedITD(bool enable) {
		listenerHRTF.SetHRTFCustomizedITD(enable);
    }
}
