#ifndef _CDISTANCE_ATTENUATOR_H_
#define _CDISTANCE_ATTENUATOR_H_

#include <Common/Buffer.h>
#include <Common/FiltersChain.h>
#include <BinauralSpatializer/ILD.h>

class CCore;

/** \details Applies distance effects (attenuation, far-distance low-pass filter and near-distance ILD) depending on the distance
   from the listener to the sound source */
class CDistanceAttenuator
{	
public:                                                             // PUBLIC METHODS

	/** \brief Default constructor
	*	\details Setup reference distance to 1.95 meters
	*/
	CDistanceAttenuator();

	/** \brief Setup the distance attenuator
	*	\details Creates the low-pass filters for far-distance (currently, 1) and setup sample rate for each filter
	*	\param [in] samplingRate sampling rate, in Hertzs
	*/
	void Setup(int samplingRate);                                

	/** \brief Get the attenuation gain due to distance for a given distance attenuation constant
	*	\param [in] doDistanceAttenuation if false, return 1.0 gain instead of computing distance attenuation
	*	\param [in] attenuationConstant distance attenuation constant, in decibels
	*	\param [in] distance distance, in meters
	*	\param [in] extraAttenuation_dB fixed attenuation (non distance-dependent) to be added, in decibels
	*	\exception May throw errors to debugger
	*/
	float GetDistanceAttenuation(bool doDistanceAttenuation, float attenuationConstant, float distance, 
		                         float extraAttennuation_dB = 0) const;

	/** \brief Process stereo input buffer to apply all distance effects (attenuation, far-distance filter and near-distance ILD)
	*	\details For use in binaural spatializer
	*	\param [in,out] buffer input and output stereo buffer
	*	\param [in] distance distance of source, in meters
	*	\param [in] attenuationConstant distance attenuation constant, in decibels
	*	\param [in] interauralAzimuthForILD interaural azimuth of source for computing near-distance ILD, in degrees
	*	\param [in] doDistanceAttenuation switch on/off distance attenuation effect
	*	\param [in] doFarDistanceLPF switch on/off far-distance low-pass filter
	*	\param [in] doILD switch on/off near-distance ILD
	*	\param [in] extraAttenuation_dB fixed attenuation (non distance-dependent) to be added, in decibels
	*	\exception May throw exceptions and errors to debugger	
	*/
	void ApplyDistanceEffects( CStereoBuffer<float> & buffer, float distance, float attenuationConstant, 
		                       float interauralAzimuthForILD, int bufferSize, int sampleRate,
		                       bool doDistanceAttenuation, bool doFarDistanceLPF, bool doILD, 
		                       float extraAttennuation_dB = 0 );

	/** \brief Process mono input buffer to apply all distance effects (attenuation, far-distance filter and near-distance ILD)
	*	\details For use in loudspeakers spatializer
	*	\param [in,out] buffer input and output stereo buffer
	*	\param [in] distance distance of source, in meters
	*	\param [in] attenuationConstant distance attenuation constant, in decibels
	*	\param [in] interauralAzimuthForILD interaural azimuth of source for computing near-distance ILD, in degrees
	*	\param [in] doDistanceAttenuation switch on/off distance attenuation effect
	*	\param [in] doFarDistanceLPF switch on/off far-distance low-pass filter
	*	\param [in] doILD switch on/off near-distance ILD 
	*	\param [in] extraAttenuation_dB fixed attenuation (non distance-dependent) to be added, in decibels
	*	\exception May throw exceptions and errors to debugger	
	*/
	void ApplyDistanceEffects( CMonoBuffer<float> & buffer, float distance,
							   float attenuationConstant, float interauralAzimuthForILD, int bufferSize, int sampleRate,
							   bool doDistanceAttenuation, bool doFarDistanceLPF, bool doILD,
		                       float extraAttennuation_dB = 0 );

	/** \brief Get the cutoff frequency of the far-distance low-pass filter, for a given distance
	*	\param [in] distance, in meters
	*	\retval cutoff cutoff frequency, in Hertzs
	*/
	float GetCutOffFreqForFarDistances(float distance);             

																	
public:                                                             // PUBLIC ATTRIBUTES

	float referenceDistance;                    ///< Distance at which the attenuation is 0 dB, in meters.

private:                                                            // PRIVATE FUNCTIONS 

	// Implementation of public ApplyDistanceEffects functions.
	void ApplyDistanceEffects( CStereoBuffer<float> *bufferStereo, CMonoBuffer<float> *bufferMono,
		                       float distance, float attenuationConstant, float interauralAzimuthForILD, int bufferSize, int sampleRate,
		                       bool doDistanceAttenuation, bool doFarDistanceLPF, bool doILD,
		                       float extraAttennuation_dB );
	
	void ApplyGain_ExponentialMovingAverage(CStereoBuffer<float> & buffer, int gain2, float alpha);

private:                                                            // PRIVATE ATTRIBUTES

	CFiltersChain distanceFiltersChain;     // It will be used to model the effect of the distance in the anechoic process.
	CILD          ILD;                      // It models the effect of distance when the sound source is close to the listener.

	float previousFrameAttenuation;				// Store the gain applied to the previous frame	
	float previousAttenuation_Channel1;
	float previousAttenuation_Channel2;
};

#endif
