/*	\brief Start a new HRTF configuration.
*	\param _HRIRlength, integer that indicates the HRIR length
*
* When this method is called: (1) Reset the previous HRTF, (2) Block the spatialization algorithm
*/
void CHRTF::BeginSetup(int32_t _HRIRlength, int32_t _bufferSize, int32_t _sampleRate){

	SET_RESULT(RESULT_OK, "HRTF Setup started");

	HRIRlength = _HRIRlength;
	bufferSize = _bufferSize;
	sampleRate = _sampleRate;

	//Clear every table
	t_HRTF_DataBase.clear();
	t_HRTF_ResampleInterpolationMethod.clear();

	setupInProgress = true;
}

/* \brief Set a new HRIR in the HRTF matrix
*	\param azimuth
*	\param elevation
*	\param newHrir
*/
void CHRTF::AddHRIR (float azimuth, float elevation, HRIR_struct && newHRIR)
{
	int iAzimuth = (int)round(azimuth / azimuthStep) * azimuthStep;
	int iElevation = (int)round(elevation / elevationStep) * elevationStep;

  printf("CHRTF::AddHRIR()\n");
  printf("iAzimuth = %i \n", iAzimuth);
  printf("iElevation = %i \n", iElevation);
  printf("newHRIR.leftDelay = %llu \n", newHRIR.leftDelay);
  printf("newHRIR.rightDelay = %llu \n", newHRIR.rightDelay);
  printf("newHRIR.leftHRIR.size() = %u \n", newHRIR.leftHRIR.size());
  printf("newHRIR.rightHRIR.size() = %u \n", newHRIR.rightHRIR.size());

	auto returnValue = t_HRTF_DataBase.emplace(orientation(iAzimuth, iElevation), std::forward<HRIR_struct>(newHRIR));
	if (returnValue.second) {
    printf("HRIR emplaced into t_HRTF_DataBase succesfully \n");
		SET_RESULT(RESULT_OK, "HRIR emplaced into t_HRTF_DataBase succesfully");
	}
	else
	{
    printf("Error emplacing HRIR in t_HRTF_DataBase map\n");
		SET_RESULT(RESULT_WARNING, "Error emplacing HRIR in t_HRTF_DataBase map");
	}
}

/* \brief Set a new HRIR in the HRTF matrix
*	\param newTable
*/
void CHRTF::AddHRTFTable(HRTFTable_type && newTable)
{
	t_HRTF_DataBase = newTable;
}

/* \brief Stop the HRTF configuration and resample the HRTF matrix
*   \return boolean that is true when the process finish correctly
*
* When this method is called: (1) Delete the common delay of every HRIR of the DataBase table,
* (2) Calculate resamplse tables performing different algorithms,
* (3) Unblock the spatialization algoritms.
*/
bool CHRTF::EndSetup(){

	// If you want to show this message, do it in your app before calling this method:
	//std::cout << std::endl << "Resampling HRTF Matrix..." << std::endl;
	// The same for the OK result...

	if (!t_HRTF_DataBase.empty())
	{
		//Delete the common delay of every HRIR functions of the DataBase Table
		DeleteCommonDelay_HRTFDataBaseTable();

		//HRTF Resampling methdos
		FillOut_HRTFDatabaseTableInPoles();	//Specific method for LISTEN DataBase
		//Resample_HRTFTable_NearestMethod(resampleStep);
		Resample_HRTFTable_InterpolationMethod(resampleStep);

		setupInProgress = false;

		SET_RESULT(RESULT_OK, "HRTF Matrix resample completed succesfully");
	}
	else
	{
		// TO DO: Should be ASSERT?
		SET_RESULT(RESULT_ERROR_NOTSET, "The t_HRTF_DataBase map has not been set");
	}
	return !setupInProgress;
}


