const HRTFTable_type & CHRTF::GetRawHRTFTable() const
{
	return t_HRTF_DataBase;
}

const HRIRbothEars_type CHRTF::GetHRIR(float azimuth, float elevation, bool runTimeInterpolation) const
{
	oneEarHRIR_struct s_leftHRIR, s_rightHRIR;
	CMonoBuffer<float> newLeftHRIR, newRightHRIR;
	HRIRbothEars_type newHRIR;

	if (!setupInProgress) {
		// TODO: STUDY the orientation round
		int iazimuth = static_cast<int>(round(azimuth));
		int ielevation = static_cast<int>(round(elevation));
		//FIXME!!
		if (iazimuth == 360) { iazimuth = 0; }
		if (ielevation == 360) { ielevation = 0; }
		if (ielevation == 90.0f) { iazimuth = 0; } // The HRIR with elevation 90 degrees is the same for every azimuth (north pole point)
		if (ielevation == 270.0f) { iazimuth = 0; } // The HRIR with elevation 270 degrees is the same for every azimuth (south pole point)
													//END TODO & FIXME

													//Run time interpolation ON
		if (runTimeInterpolation)
		{
			auto it = t_HRTF_ResampleInterpolationMethod.find(orientation(iazimuth, ielevation));
			if (it != t_HRTF_ResampleInterpolationMethod.end()) {
				//Left Ear
				s_leftHRIR.delay = it->second.leftDelay;
				s_leftHRIR.HRIR = it->second.leftHRIR;
				newHRIR.first = AddDelayToHRIR(LEFT, s_leftHRIR, azimuth, elevation);
				//Right Ear
				s_rightHRIR.delay = it->second.rightDelay;
				s_rightHRIR.HRIR = it->second.rightHRIR;
				newHRIR.second = AddDelayToHRIR(RIGHT, s_rightHRIR, azimuth, elevation);

				return newHRIR;
			}
			else
			{
				//Left Ear
				s_leftHRIR = CalculateHRIR_QuadrantInterpolation_InterpolatedResampleTable(LEFT, iazimuth, ielevation);
				newHRIR.first = AddDelayToHRIR(LEFT, s_leftHRIR, azimuth, elevation);
				//Right Ear
				s_rightHRIR = CalculateHRIR_QuadrantInterpolation_InterpolatedResampleTable(RIGHT, iazimuth, ielevation);
				newHRIR.second = AddDelayToHRIR(RIGHT, s_rightHRIR, azimuth, elevation);

				return newHRIR;
			}
		}
		//Run time interpolation OFF
		else
		{
			//1. Calculate the nearest orientation__________________________________________________________________________
			int nearestAzimuth = static_cast <int> (round(azimuth / resampleStep) * resampleStep);
			int nearestElevation = static_cast <int> (round(elevation / resampleStep) * resampleStep);
			// FIX ME
			if (nearestAzimuth == 360) { nearestAzimuth = 0; }
			if (nearestElevation == 360) { nearestElevation = 0; }
			// END FIX ME

			auto it = t_HRTF_ResampleInterpolationMethod.find(orientation(nearestAzimuth, nearestElevation));
			if (it != t_HRTF_ResampleInterpolationMethod.end())
			{
				//Left Ear
				s_leftHRIR.delay = it->second.leftDelay;
				s_leftHRIR.HRIR = it->second.leftHRIR;
				newHRIR.first = AddDelayToHRIR(LEFT, s_leftHRIR, azimuth, elevation);
				//Right Ear
				s_rightHRIR.delay = it->second.rightDelay;
				s_rightHRIR.HRIR = it->second.rightHRIR;
				newHRIR.second = AddDelayToHRIR(RIGHT, s_rightHRIR, azimuth, elevation);

				return newHRIR;
			}
			else
			{
				SET_RESULT(RESULT_WARNING, "Get_HRIR: HRIR not found. Return empty");
				return emptyHRIRbothEars;
			}
		}
	}
	else
	{
		SET_RESULT(RESULT_ERROR_NOTSET, "Get_HRIR: HRTF Set up in progress");
		return emptyHRIRbothEars;
	}

	SET_RESULT(RESULT_WARNING, "Get_HRIR: Table t_HRTF_ResampleInterpolationMethod does not contain the requested HRIR. Get_HRIR return empty");
	return emptyHRIRbothEars;
}
const CMonoBuffer<float> CHRTF::GetHRIR_left(float _azimuth, float _elevation, bool runTimeInterpolation) const
{
	oneEarHRIR_struct s_leftHRIR;
	CMonoBuffer<float> newLeftHRIR;

	if (!setupInProgress) {
		// TODO: STUDY the orientation round
		int iazimuth = static_cast<int>(round(_azimuth));
		int ielevation = static_cast<int>(round(_elevation));
		//FIXME
		if (iazimuth == 360) { iazimuth = 0; }
		if (ielevation == 360) { ielevation = 0; }
		if (ielevation == 90.0f || ielevation == 270.0f) { iazimuth = 0; } // The HRIR with elevation 90 or 270 degrees is the same for every azimuth (north pole point)
																		   //END TODO AND FIXME

																		   //Run time interpolation ON
		if (runTimeInterpolation)
		{
			auto it = t_HRTF_ResampleInterpolationMethod.find(orientation(iazimuth, ielevation));
			if (it != t_HRTF_ResampleInterpolationMethod.end())
			{
				s_leftHRIR.delay = it->second.leftDelay;
				s_leftHRIR.HRIR = it->second.leftHRIR;
				newLeftHRIR = AddDelayToHRIR(LEFT, s_leftHRIR, _azimuth, _elevation);
				return newLeftHRIR;
			}
			else
			{
				s_leftHRIR = CalculateHRIR_QuadrantInterpolation_InterpolatedResampleTable(LEFT, iazimuth, ielevation);
				newLeftHRIR = AddDelayToHRIR(LEFT, s_leftHRIR, _azimuth, _elevation);
				return newLeftHRIR;
			}
		}
		else
		{
			//1. Calculate the nearest orientation
			int nearestAzimuth = (int)round(_azimuth / resampleStep) * resampleStep;
			int nearestElevation = (int)round(_elevation / resampleStep) * resampleStep;
			if (nearestAzimuth == 360) { nearestAzimuth = 0; }
			if (nearestElevation == 360) { nearestElevation = 0; }

			//2. Search the orientation in the Interpolation table
			auto it = t_HRTF_ResampleInterpolationMethod.find(orientation(nearestAzimuth, nearestElevation));
			if (it != t_HRTF_ResampleInterpolationMethod.end())
			{
				s_leftHRIR.delay = it->second.leftDelay;
				s_leftHRIR.HRIR = it->second.leftHRIR;
				newLeftHRIR = AddDelayToHRIR(LEFT, s_leftHRIR, _azimuth, _elevation);
				return newLeftHRIR;
			}
			else {
				SET_RESULT(RESULT_ERROR_NOTSET, "GetHRIR_left: HRIR not found");
				return emptyMonoBuffer;
			}
		}
	}
	else
	{
		SET_RESULT(RESULT_ERROR_NOTSET, "GetHRIR_left: HRTF Set up in progress");
		return emptyMonoBuffer;
	}
	SET_RESULT(RESULT_WARNING, "GetHRIR_left return empty");
	return emptyMonoBuffer;
}
