/* \brief Fill out the HRTF for every azimuth and two specific elevations: 90 and 270 degrees for the LISTEN HRTF DataBase
*/
void CHRTF::FillOut_HRTFDatabaseTableInPoles() {

	// FIXME: This method is specific for the LISTEN DATA BASE

	HRIR_struct precalculatedHRIR_270, copiedHRIR_90;	//temporal vrble to store HRIR
	bool firstTime270 = true;
	//int HRIRsize;

	//** 90 degrees elevation
	auto it90 = t_HRTF_DataBase.find(orientation(0, 90));
	if (it90 != t_HRTF_DataBase.end()) {
		copiedHRIR_90 = it90->second;
		//SET_RESULT(RESULT_OK, "");
	}
	else {
		//std::cout << "HRIR with orientation(0, 90) in table t_HRTF_DataBase not found" << std::endl;
		SET_RESULT(RESULT_WARNING, "HRIR with orientation(0, 90) in table t_HRTF_DataBase not found");
	}

	//** 270 degrees elevation
	if (t_HRTF_DataBase.find(orientation(0, 270)) == t_HRTF_DataBase.end()) {//If HRIR with orientation (0,270) doesn't exist in t_HRTF_DataBase
																					 // Calculate the HRIR for elevation = 270 degrees as the weithed sum of all HRIRs with elevation 315 degrees
		float HRIR_cnt = 0.0f;	// HRIR counter necesary to do the weithed sum
		for (auto it = t_HRTF_DataBase.begin(); it != t_HRTF_DataBase.end(); ++it)
		{
			//HRIRsize = it->second.first.GetLength();
			//HRIRsize = HRIRlength;
			if (it->first.elevation == 315)
			{
				if (firstTime270) {
					precalculatedHRIR_270.leftHRIR = it->second.leftHRIR;		// leftHRIR
					precalculatedHRIR_270.leftDelay = it->second.leftDelay;		// leftDelay
					precalculatedHRIR_270.rightHRIR = it->second.rightHRIR;		// rightHRIR
					precalculatedHRIR_270.rightDelay = it->second.rightDelay;	// rightDelay
					HRIR_cnt++;
					firstTime270 = false;
				}
				else
				{
					for (int i = 0; i < HRIRlength; i++) {
						precalculatedHRIR_270.leftHRIR[i] = precalculatedHRIR_270.leftHRIR[i] + it->second.leftHRIR[i];		// leftHRIR
						precalculatedHRIR_270.rightHRIR[i] = precalculatedHRIR_270.rightHRIR[i] + it->second.rightHRIR[i];	// rightHRIR
					}
					precalculatedHRIR_270.leftDelay = precalculatedHRIR_270.leftDelay + it->second.leftDelay;		// leftDelay
					precalculatedHRIR_270.rightDelay = precalculatedHRIR_270.rightDelay + it->second.rightDelay;	// rightDelay
					HRIR_cnt++;
				}
			}
			//else {

			//	std::cout << "The HRTF DataBase table do not contain values for HRIR with elevation 315 degrees" << std::endl;
			//}
		}

		for (int i = 0; i < HRIRlength; i++) {
			precalculatedHRIR_270.leftHRIR[i] = (1 / HRIR_cnt) * precalculatedHRIR_270.leftHRIR[i];		// leftHRIR
			precalculatedHRIR_270.rightHRIR[i] = (1 / HRIR_cnt) * precalculatedHRIR_270.rightHRIR[i];	// rightHRIR
		}
		//TODO: Doing a round it's a good idea?
		precalculatedHRIR_270.leftDelay = static_cast <unsigned long> (round((1 / HRIR_cnt) * precalculatedHRIR_270.leftDelay));		// leftDelay
		precalculatedHRIR_270.rightDelay = static_cast <unsigned long> (round((1 / HRIR_cnt) * precalculatedHRIR_270.rightDelay));	// rightDelay


	}
	else //If HRIR with orientation (0,270) exist in t_HRTF_DataBase
	{
		auto it270 = t_HRTF_DataBase.find(orientation(0, 270));
		precalculatedHRIR_270 = it270->second;
	}

	// Fill out the table
	for (int i = 0; i < 360; i = i + 15) {
		//Elevation 270 degrees
		t_HRTF_DataBase.emplace(orientation(i, 270), precalculatedHRIR_270);
		//Elevation 90 degrees
		t_HRTF_DataBase.emplace(orientation(i, 90), copiedHRIR_90);
		//Azimuth 360 degrees
		auto it0 = t_HRTF_DataBase.find(orientation(0, i));
		if (it0 != t_HRTF_DataBase.end()) {
			t_HRTF_DataBase.emplace(orientation(360, i), it0->second);
		}
	}
}


