/* \brief Add the delay to the HRIR function
*	\param ear, enum that indicates the listener ear {LEFT, RIGHT}
*	\param HRIR, HRIR function and delay separately
*	\param _azimuth, source polar azimuth
*	\param _elevation, source polar elevation
*   \return HRIR function with delay incorporated
*/
const CMonoBuffer<float> CHRTF::AddDelayToHRIR(T_ear ear, oneEarHRIR_struct HRIR, float _azimuth, float _elevation)const {

	//TODO: TEMP ********************
	float sampleRate = 44100;
	//***********************************

	float rAzimuth = _azimuth * PI / 180;
	float rElevation = _elevation * PI / 180;

	//Copy the HRIR function
	CMonoBuffer<float> HRIR_withDelay = HRIR.HRIR;

	if (enableCustomizedITD)
	{
		//Calculate the customized delay
		unsigned long customizedDelay = 0;
		float interauralAzimuth = asin(sin(rAzimuth) * cos(rElevation));
		float ITD = CalculateITDFromHeadRadius(listenerHeadRadius, interauralAzimuth);

		if ((ITD > 0 && ear == RIGHT)  || (ITD < 0 && ear == LEFT)) {
			customizedDelay =  static_cast <unsigned long> (round(abs(sampleRate * ITD)));
		}
		//else { customizedDelay = 0; }

		// Calculate the delay samples vector (vector with zero values and 'delay' size)
		std::vector<double> vectorDelay(customizedDelay, 0.0f);
		// Add the delay vector at the begin of the HRIR function
		HRIR_withDelay.insert(HRIR_withDelay.begin(), vectorDelay.begin(), vectorDelay.end());
	}
	else {

		// Calculate the delay samples vector (vector with zero values and 'delay' size)
		std::vector<double> vectorDelay(HRIR.delay, 0.0f);
		// Add the delay vector at the begin of the HRIR function
		HRIR_withDelay.insert(HRIR_withDelay.begin(), vectorDelay.begin(), vectorDelay.end());
	}

	// Take only HRIRlenght samples
	//TODO: find an optimized way to split a vector
	CMonoBuffer<float> split_HRIR_withDelay(HRIR_withDelay.begin(), HRIR_withDelay.begin() + HRIRlength);

	SET_RESULT(RESULT_OK, "Delay added to HRIR succesfully");

	return split_HRIR_withDelay;
}


/*  \brief Calculate and Delete the common delay of every HRIR functions of the DataBase Table. Off line Method, called from EndSetUp()
*   \return the common delay for left and right ears
*/
void CHRTF::DeleteCommonDelay_HRTFDataBaseTable()
{
	//1. Init the minumun value with the fist value of the table
	auto it0 = t_HRTF_DataBase.begin();
	unsigned long minimumDelayLeft = it0->second.leftDelay;		//Vrbl to store the minumun delay value for left ear
	unsigned long minimumDelayRight = it0->second.rightDelay;	//Vrbl to store the minumun delay value for right ear

	//2. Find the common delay
	//Scan the whole table looking for the minimum delay for left and right ears
	for (auto it = t_HRTF_DataBase.begin(); it != t_HRTF_DataBase.end(); it++) {
		//Left ear
		if (it->second.leftDelay < minimumDelayLeft) {
			minimumDelayLeft = it->second.leftDelay;
		}
		//Right ear
		if (it->second.rightDelay < minimumDelayRight) {
			minimumDelayRight = it->second.rightDelay;
		}
	}

	//3. Delete the common delay
	//Scan the whole table substracting the common delay to every delays for both ears
	for (auto it = t_HRTF_DataBase.begin(); it != t_HRTF_DataBase.end(); it++)
	{
		it->second.leftDelay = it->second.leftDelay - minimumDelayLeft;		//Left ear
		it->second.rightDelay = it->second.rightDelay - minimumDelayRight;	//Right ear
	}

	SET_RESULT(RESULT_OK, "Common delay deleted from HRTF table succesfully");
}


/* \brief Set the radious of the listener head, which is configured in the App, read by the core and set by the core
*	\param _listenerHeadRadious, radious of the listener head
*/
void CHRTF::SetListenerHeadRadius(float _listenerHeadRadius) {
	listenerHeadRadius = _listenerHeadRadius;
}

/* \brief Allow to calculate and add to the HRIR a customized delay
*   IMPORTANT NOTE: The customized ITD can be enable if the HRIR function do not contain the delay.
*	\param _enableCustomizeITD, true if the HRTF will use the customized delay
*/
void CHRTF::SetHRTFCustomizedITD(bool _enableCustomizeITD) {
	enableCustomizedITD = _enableCustomizeITD;
}


/*  \brief Calculate the ITD using the Lord Rayleight formula which depend on the interaural azimuth and the listener head radious
*	\param _headRadious, listener head radius, set by the App
*   \param  _interauralAzimuth, source interaural azimuth
*   \return customizated ITD
*/
const float CHRTF::CalculateITDFromHeadRadius(float _headRadius, float _interauralAzimuth) const
{
	//Calculate the ITD (from https://www.lpi.tel.uva.es/~nacho/docencia/ing_ond_1/trabajos_05_06/io5/public_html/ & http://interface.cipic.ucdavis.edu/sound/tutorial/psych.html)
	float ITD = _headRadius * (_interauralAzimuth + sin(_interauralAzimuth)) / 343.0f; //ownerCore->GetMagnitudes().GetSoundSpeed(); //_azimuth in radians!
	return ITD;
}


