/*! \file */

// Structs and types definitions 

/** \brief Defines and holds data to work with orientations
*/
struct orientation
{
	int32_t azimuth;	///< Azimuth angle in degrees
	int32_t elevation;	///< Elevation angle in degrees
    orientation(int32_t _azimuth, int32_t _elevation):azimuth{_azimuth}, elevation{_elevation}{}
    orientation():orientation{0,0}{}
    bool operator==(const orientation& oth) const
    {
        return ((this->azimuth == oth.azimuth) && (this->elevation == oth.elevation));
    }
};

/** \brief Type definition for a left-right pair of impulse response with custom delay for ITD 
*/
struct HRIR_struct {
	uint64_t leftDelay;				///< Left delay, in number of samples
	uint64_t rightDelay;			///< Right delay, in number of samples
	CMonoBuffer<float> leftHRIR;	///< Left impulse response data
	CMonoBuffer<float> rightHRIR;	///< Right impulse response data
};

/** \brief Type definition for an impulse response with custom delay for ITD
*/
struct oneEarHRIR_struct {
	uint64_t delay;				///< Delay, in number of samples
	CMonoBuffer<float> HRIR;	///< Impulse response data
};

/**	\brief Type definition for barycentric coordinates
*/
struct barycentricCoordinates_struct {
	float alpha;	///< Coordinate alpha
	float beta;		///< Coordinate beta
	float gamma;	///< Coordinate gamma
};

/** \brief Type definition for specifying one ear
*/
enum T_ear {LEFT,	///< Left ear
			RIGHT	///< Right ear
			};

namespace std
{
	//[TBC]
    template<>
    struct hash<orientation>
    {
        // adapted from http://en.cppreference.com/w/cpp/utility/hash
        size_t operator()(const orientation & key) const
        {
            size_t h1 = std::hash<int32_t>()(key.azimuth);
            size_t h2 = std::hash<int32_t>()(key.elevation);
            return h1 ^ (h2 << 1);  // exclusive or of hash functions for each int.
        }
    };
}

/** \brief Type definition for the impulse response of both ears, without delay
*/
typedef std::pair<CMonoBuffer<float>,CMonoBuffer<float>> HRIRbothEars_type;

/** \brief Type definition for the delay of both ears
*/
typedef std::pair<unsigned long, unsigned long> DelayBothEars_type;

/** \brief Type definition for the HRTF table
*/
typedef std::unordered_map<orientation, HRIR_struct> HRTFTable_type;

/** \brief Type definition for a distance-orientation pair
*/
typedef std::pair <float, orientation> Pair_DistanceOrientation_type;

class CCore;

/** \details This class gets impulse response data to compose HRTFs and implements different algorithms to interpolate the HRIR functions.
*/
class CHRTF
{
// METHODS
public:

	/** \brief Constructor.
	*	\details By default, customized ITD is switched off
	*	\param [in] _azimuthStep quantization step at which azimuth is sampled in the original database, in degrees
	*	\param [in] _elevationStep quantization step at which elevation is sampled in the original database, in degrees
	*/
    CHRTF(int32_t _azimuthStep, int32_t _elevationStep):
	azimuthStep{_azimuthStep}, 
	elevationStep{_elevationStep}		
    {
		//Init 
		enableCustomizedITD = false;
	}
    
	/** \brief Default constructor.
	*	\details By default, sets quantization steps for azimuth and elevation to 15
	*/
	CHRTF() : CHRTF{ 15,15 } 
    { }

	/** \brief Get azimuth quantization step 
	*	\retval step azimuth quantization step
	*/
	int32_t GetAzimuthStep() const 
	{
		return azimuthStep;
	}

	/** \brief Get elevation quantization step
	*	\retval step elevation quantization step
	*/
	int32_t GetElevationStep() const 
	{
		return elevationStep;
	}

	/** \brief Get size of each HRIR buffer
	*	\retval size number of samples of each HRIR buffer for one ear
	*/
	int32_t GetHRIRLength() const
	{
		return HRIRlength;
	}
		
	/** \brief Start a new HRTF configuration
	*	\param [in] _HRIRlength buffer size of the HRIR to be added
	*	\param [in] _bufferSize buffer size of the input signal that is going to be used in the audio loop
	*	\note This method is implemented in CoreState class
	*	\sa CoreState
	*/
	void BeginSetup(int32_t _HRIRlength, int32_t _bufferSize, int32_t _sampleRate);

	/** \brief Set the full HRIR matrix. 
	*	\param [in] newTable full table with all HRIR data
	*/
	void AddHRTFTable(HRTFTable_type && newTable);							

	/** \brief Add a new HRIR to the HRTF table
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] newHRIR HRIR data for both ears
	*   \throws May throw warnings to debugger
	*/
	void AddHRIR(float azimuth, float elevation, HRIR_struct && newHRIR);	

	/** \brief Stop the HRTF configuration	
	*	\retval success Setup has ended
	*   \throws May throw errors to debugger
	*/
	bool EndSetup();														
						
	/** \brief Get HRIR buffer with Delay from database, for left ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] interpolate interpolation method (0: no interpolation; 1: Only pre-interpolation; 2: Only runtime-interpolation; 3: Both pre- and runtime-interpolation)
	*	\retval HRIR buffer with delay from database for left ear
	*   \throws May throw errors to debugger
	*/
	const CMonoBuffer<float> GetHRIR_fromDataBaseTableLEFT(float azimuth, float elevation, int interpolate) const;  

	/** \brief Get HRIR buffer with Delay from database, for right ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] interpolate interpolation method (0: no interpolation; 1: Only pre-interpolation; 2: Only runtime-interpolation; 3: Both pre- and runtime-interpolation)
	*	\retval HRIR buffer with delay from database for right ear
	*   \throws May throw errors to debugger
	*/
	const CMonoBuffer<float> GetHRIR_fromDataBaseTableRIGHT(float azimuth, float elevation, int interpolate) const; 

		
	/** \brief Set listener head radius 
	*	\param [in] _listenerHeadRadius listener head radius, in meters
	*/
	void SetListenerHeadRadius(float _listenerHeadRadius);	

	/** \brief Switch on/off ITD customization
	*	\param [in] _enableCustomizeITD switch on/off ITD customization
	*/
	void SetHRTFCustomizedITD(bool _enableCustomizeITD);

	/** \brief Get raw HRTF table
	*	\retval table raw HRTF table
	*/
	const HRTFTable_type & GetRawHRTFTable() const;  
    
	/** \brief Get HRIR buffer with Delay, for both ears
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] runTimeInterpolation interpolation method (false: no run-time interpolation; true: runtime-interpolation)
	*	\retval HRIR interpolated HRIR with delay for both ears
	*   \throws May throw errors to debugger
	*/
	const HRIRbothEars_type GetHRIR(float azimuth, float elevation, bool runTimeInterpolation) const;

	/** \brief Get interpolated HRIR buffer with Delay, for left ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] runTimeInterpolation interpolation method (false: no run-time interpolation; true: runtime-interpolation)
	*	\retval HRIR interpolated buffer with delay for left ear
	*   \throws May throw errors to debugger
	*/
	const CMonoBuffer<float> GetHRIR_left(float azimuth, float elevation, bool runTimeInterpolation) const;

	/** \brief Get interpolated HRIR buffer with Delay, for right ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] runTimeInterpolation interpolation method (false: no run-time interpolation; true: runtime-interpolation)
	*	\retval HRIR interpolated buffer with delay for right ear
	*   \throws May throw errors to debugger
	*/
	const CMonoBuffer<float> GetHRIR_right(float azimuth, float elevation, bool runTimeInterpolation) const;

	/** \brief Get interpolated HRIR buffer with Delay, for left ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] interpolate interpolation method (0: no interpolation; 1: Only pre-interpolation; 2: Only runtime-interpolation; 3: Both pre- and runtime-interpolation)
	*	\retval HRIR interpolated buffer with delay for left ear
	*   \throws May throw errors to debugger
	*/
	const oneEarHRIR_struct GetHRIR_left_frequency(float azimuth, float elevation, bool interpolate) const;

	/** \brief Get interpolated HRIR buffer with Delay, for right ear
	*	\param [in] azimuth azimuth angle in degrees
	*	\param [in] elevation elevation angle in degrees
	*	\param [in] interpolate interpolation method (0: no interpolation; 1: Only pre-interpolation; 2: Only runtime-interpolation; 3: Both pre- and runtime-interpolation)
	*	\retval HRIR interpolated buffer with delay for right ear
	*   \throws May throw errors to debugger
	*/
	const oneEarHRIR_struct GetHRIR_right_frequency(float azimuth, float elevation, bool interpolate) const;

private:	
	
	int32_t HRIRlength;					// HRIR vector length
	int32_t bufferSize;					// Input signal buffer size
	int32_t sampleRate;					// Sample Rate
	HRIR_struct leftHRIR;				// HRIR data of the user left ear
	HRIR_struct rightHRIR;				// HRIR data of the user right ear

	bool setupInProgress;	// Variable that indicates the HRTF add and resample algorithm are in process
	int32_t azimuthStep;		// HRTF table Database azimuth step
	int32_t elevationStep;		// HRTF table Database elevation step
	
private: 
	float listenerHeadRadius;	//Radius of the listener head, coming from the Core
									
	HRTFTable_type t_HRTF_DataBase, t_HRTF_ResampleInterpolationMethod;	// HRTF tables
	HRTFTable_type t_HRTF_ResampleInterpolationMethod_frequency;										// HRTF tables

	// Empty object to return in some methods
	HRIR_struct emptyHRIR;
	CMonoBuffer<float> emptyMonoBuffer; //std::vector<T> have constructors initializing the object to be empty
	oneEarHRIR_struct emptyOneEarHRTF;
	HRIRbothEars_type emptyHRIRbothEars;

	//FIX ME! Findf a better method to select the interpolation
	bool bInterpolatedResampleTable;	// If true: calculate the HRTF resample matrix with interpolation
	//FIX ME! Find a better place to this
	int resampleStep = 5;			// HRTF Resample table step (azimuth and elevation)
	bool enableCustomizedITD;		// Indicate the use of a customized delay

	//Private methods
	/* Methods to use in the resample algortihms*/
	void FillOut_HRTFDatabaseTableInPoles();
	void Resample_HRTFTable_InterpolationMethod(int resampleStep);
	HRIR_struct CalculateHRIR_InterpolationMethod(int newAzimuth, int newElevation);
	float HaversineFormula(float azimuth1, float elevation1, float azimuth2, float elevation2);
	const barycentricCoordinates_struct GetBarycentricCoordinates(int newAzimuth, int newElevation, float x1, float y1, float x2, float y2, float x3, float y3) const;
	float TransformAzimuth(float azimuthOrientationOfInterest, float originalAzimuth);
	float TransformElevation(float elevationOrientationOfInterest, float originalElevation);
	std::list<Pair_DistanceOrientation_type> GetSortedDistancesList(int newAzimuth, int newElevation);
	
	/* Methods to use in the get HRIR run-time algortihms*/
	const oneEarHRIR_struct CalculateHRIR_QuadrantInterpolation_InterpolatedResampleTable(T_ear ear, int azimuth, int elevation) const;
	const oneEarHRIR_struct CalculateHRIR_QuadrantInterpolation_InterpolatedResampleTable(T_ear ear, int azimuth, int elevation, bool freq) const;
	const oneEarHRIR_struct CalculateHRIRFromBarycentricCoordinates_InterpolatedResampleTable(T_ear ear, barycentricCoordinates_struct barycentricCoordinates, orientation orientation_pto1, orientation orientation_pto2, orientation orientation_pto3) const;
	const oneEarHRIR_struct CalculateHRIRFromBarycentricCoordinates_InterpolatedResampleTable(T_ear ear, barycentricCoordinates_struct barycentricCoordinates, orientation orientation_pto1, orientation orientation_pto2, orientation orientation_pto3, bool freq) const;	
	const CMonoBuffer<float> AddDelayToHRIR(T_ear ear, oneEarHRIR_struct HRIR, float _azimuth, float _elevation)const;
	void DeleteCommonDelay_HRTFDataBaseTable();
	const  float CalculateITDFromHeadRadius(float _headRadius, float _interauralAzimuth)const;
	const unsigned long GetCustomizedDelay(float _azimuth, float _elevation, T_ear ear)const;
};

#endif
