	//Initialize the class and allocate memory.
	void CFconvolver::Setup(int _inputSize, int _AIRSize)
	{
		ASSERT(_inputSize > 0, RESULT_ERROR_BADSIZE, "Bad input size when setting up frequency convolver", "");
		ASSERT(_AIRSize > 0, RESULT_ERROR_BADSIZE, "Bad ABIR size when setting up frequency convolver", "");

		inputSize = _inputSize;				
		IRSize = _AIRSize;					
		FFTBufferSize = inputSize + IRSize;			//FIXME Could be shorter -1;

		//Check if if power of two, if not round up to the next highest power of 2 
		if (!IsPowerOfTwo(FFTBufferSize)) {
			FFTBufferSize = NextPowerOfTwo(FFTBufferSize);
		}
		storageBuffer.resize(FFTBufferSize);		//Prepare the buffer with the space that we are going to needed
		normalizeCoef = 1.0f/FFTBufferSize;				//Store the normalize coef for the FFT-1
		FFTBufferSize *= 2;							//We multiplicate by 2 because we need to store real and imaginary part

		ip_size = sqrt(FFTBufferSize / 2) + 2;		//Size of the auxiliary array w. This come from lib documentation/examples.
		w_size = FFTBufferSize * 5 / 4;				//Size of the auxiliary array w. This come from lib documentation/examples.

#ifdef USE_PROFILER_FConvolver
		PROFILER3DTI.SetAutomaticWrite(dataSetFFT, "PROF_FConvolver_FFT.txt");
		PROFILER3DTI.SetAutomaticWrite(dataSetComplexMult, "PROF_FConvolver_ComplexMult.txt");
		PROFILER3DTI.SetAutomaticWrite(dataSetIFFT, "PROF_FConvolver_IFFT.txt");
		PROFILER3DTI.SetAutomaticWrite(dataSetOthers, "PROF_FConvolver_Others.txt");
		PROFILER3DTI.StartRelativeSampling(dataSetFFT);
		PROFILER3DTI.StartRelativeSampling(dataSetComplexMult);
		PROFILER3DTI.StartRelativeSampling(dataSetIFFT);
		PROFILER3DTI.StartRelativeSampling(dataSetOthers);
#endif

		SET_RESULT(RESULT_OK, "Frequency convolver succesfully set");
	}//Setup

	 //Get a the FFT of the input signal
	void CFconvolver::GetFFT(const std::vector<float>& inputSource_time, std::vector<float>& inputSource_frequency, int _IRSize)
	{
		int _inputSize = inputSource_time.size();
		
		ASSERT(_inputSize > 0, RESULT_ERROR_BADSIZE, "Bad input size when setting up frequency convolver", "");
		ASSERT(_IRSize > 0, RESULT_ERROR_BADSIZE, "Bad ABIR size when setting up frequency convolver", "");
		//SET_RESULT(RESULT_OK, "Frequency convolver succesfully set");
		
		int FFTBufferSize = _inputSize + _IRSize;	//FIXME Could be shorter -1;

											//Check if if power of two, if not round up to the next highest power of 2 
		if (!IsPowerOfTwo(FFTBufferSize)) {
			FFTBufferSize = NextPowerOfTwo(FFTBufferSize);
		}
		std::vector<double> storageBuffer;
		storageBuffer.resize(FFTBufferSize);		//Prepare the buffer with the space that we are going to needed
		FFTBufferSize *= 2;							//We multiplicate by 2 because we need to store real and imaginary part

		int ip_size = sqrt(FFTBufferSize / 2) + 2;		//Size of the auxiliary array w. This come from lib documentation/examples.
		int w_size = FFTBufferSize * 5 / 4;				//Size of the auxiliary array w. This come from lib documentation/examples.
				

		//Initialize the vector of doubles to Store the AmbiBinIR
		std::vector<double> inputSource_FFT(FFTBufferSize);

		//Copy the input vector into an vector of doubles and insert the imaginary part.			
		AddImaginaryPart(inputSource_time, inputSource_FFT);

		//Prepare the FFT						
		std::vector<int> ip(ip_size);	//Define the auxiliary array ip 
		std::vector<double> w(w_size);	//Define the auxiliary array w			
		ip[0] = 0;						//w[],ip[] are initialized if ip[0] == 0.

										//Make the FFT
		cdft(FFTBufferSize, 1, inputSource_FFT.data(), ip.data(), w.data());

		//Copy to the output float vector			
		inputSource_frequency.resize(FFTBufferSize);
		for (int i = 0; i < inputSource_FFT.size(); i++) {
			inputSource_frequency[i] = static_cast<float>(inputSource_FFT[i]);
		}
	}

	//Gets the FFT of an impulse response
	void CFconvolver::GetFFT_IR(const CMonoBuffer<float>& IR_time, CMonoBuffer<float>& IR_frequency)
	{
		//Check if the size is the same that the has been initialize with the setup method
		ASSERT(IR_time.size() == IRSize, RESULT_ERROR_BADSIZE, "Buffer size does not agree with the size specified in the setup of the frequency convolver", "");

		//Initialize the vector of doubles to Store the AmbiBinIR
		std::vector<double> IR_FFT(FFTBufferSize);

		//Copy the input vector into an vector of doubles and insert the imaginary part.			
		AddImaginaryPart(IR_time, IR_FFT);

		//Prepare the FFT						
		std::vector<int> ip(ip_size);	//Define the auxiliary array ip 
		std::vector<double> w(w_size);	//Define the auxiliary array w			
		ip[0] = 0;						//w[],ip[] are initialized if ip[0] == 0.

		//Make the FFT
		cdft(FFTBufferSize, 1, IR_FFT.data(), ip.data(), w.data());

		//Copy to the output float vector			
		IR_frequency.resize(FFTBufferSize);
		for (int i = 0; i < IR_FFT.size(); i++)
		{
			IR_frequency[i] = static_cast<float>(IR_FFT[i]);
		}
	}//GetFFT_AmbiBinIR

	//Get a the FFT of the input signal
	void CFconvolver::GetFFT_Input(const std::vector<float>& inputSource_time, std::vector<float>& inputSource_frequency)
	{
		//Check if the size is the same that was initialized with the setup method
		ASSERT(inputSource_time.size() == inputSize, RESULT_ERROR_BADSIZE, "Buffer size does not agree with the size specified in the setup of the frequency convolver", "");

		//Initialize the vector of doubles to Store the AmbiBinIR
		std::vector<double> inputSource_FFT(FFTBufferSize);

		//Copy the input vector into an vector of doubles and insert the imaginary part.			
		AddImaginaryPart(inputSource_time, inputSource_FFT);

		//Prepare the FFT						
		std::vector<int> ip(ip_size);	//Define the auxiliary array ip 
		std::vector<double> w(w_size);	//Define the auxiliary array w			
		ip[0] = 0;						//w[],ip[] are initialized if ip[0] == 0.

										//Make the FFT
		cdft(FFTBufferSize, 1, inputSource_FFT.data(), ip.data(), w.data());

		//Copy to the output float vector			
		inputSource_frequency.resize(FFTBufferSize);
		for (int i = 0; i < inputSource_FFT.size(); i++) {
			inputSource_frequency[i] = static_cast<float>(inputSource_FFT[i]);
		}
	};

	//This method does the complex multiplication between the vector elements. Both vectors have to be the same size
	void CFconvolver::ComplexMultiplication(const std::vector<float>& x, const std::vector<float>& h, std::vector<float>& y)
	{
		ASSERT(x.size() == h.size(), RESULT_ERROR_BADSIZE, "Complex multiplication in frequency convolver requires two vectors of the same size", "");

		y.resize(x.size());
		for (int i = 0; i < (y.size()*0.5f); i++)
		{
			float a = x[2 * i];
			float b = x[2 * i + 1];
			float c = (h[2 * i]);
			float d = (h[2 * i + 1]);

			y[2 * i] = a*c - b*d;
			y[2 * i + 1] = a*d + b*c;
		}
	}//ComplexMultiplicaton

	//Get the IFFT of the output signal
	void CFconvolver::GetIFFT_Output(const std::vector<float>& inputBuffer_frequency, std::vector<float>& outputBuffer_time, int delayDifference)
	{
		ASSERT(inputBuffer_frequency.size() == FFTBufferSize, RESULT_ERROR_BADSIZE, "Incorrect size of input buffer when computing inverse FFT in frequency convolver", "");

		//Convert to double
		std::vector<double> outBuffer_temp(inputBuffer_frequency.begin(), inputBuffer_frequency.end());

		//Prepare the FFT						
		std::vector<int> ip(ip_size);	//Define the auxiliary array ip 
		std::vector<double> w(w_size);	//Define the auxiliary array w			
		ip[0] = 0;						//w[],ip[] are initialized if ip[0] == 0.

		//Make the FFT-1
		cdft(FFTBufferSize, -1, outBuffer_temp.data(), ip.data(), w.data());
		//Get the audio output buffer
		CalculateOutputBuffer_OverlapAddMethod(outBuffer_temp, outputBuffer_time, delayDifference);
	}//GetIFFT
	
