#include <BinauralSpatializer/ILD.h>
#include <Common/Debugger.h>

ILD_HashTable CILD::ILD_Data;
int CILD::Default_ILD_AzimuthStep = 5;
int CILD::Default_ILD_DistanceStep = 10;

//////////////////////////////////////////////
	CILD::CILD()
	{
		FiltersLeft .AddFilter();
		FiltersLeft .AddFilter();

		FiltersRight.AddFilter();
		FiltersRight.AddFilter();
	}

//////////////////////////////////////////////
	// Aplies ILD to audio samples in buffer
	void CILD::Process( CStereoBuffer<float> & buffer, float distance_m, float azimuth )
	{
		//if (buffer.size() <= 0)
		//{
		//	SET_RESULT(RESULT_ERROR_INVALID_PARAM, "Buffer is empty");
		//	return;
		//}
		//else if (distance_m <= 0 || azimuth < -90.0 || azimuth > 90)
		//{
		//	SET_RESULT(RESULT_ERROR_INVALID_PARAM, "Distance must be greater than zero and azimuth range is [-90, 90]");
		//	return;
		//}
		//else if (Def_ILD_AzimuthStep <= 0 || Def_ILD_DistanceStep <= 0 )
		//{
		//	SET_RESULT( RESULT_ERROR_INVALID_PARAM, "Step values of ILD hash table are not valid.");
		//	return;
		//}
		//else if ( ILD_Data == NULL )
		//{
		//	SET_RESULT(RESULT_ERROR_NOTINITIALIZED, "A Hash Table should be provided through CILD::SetILD_HashTable()");
		//	return;
		//}
		//else
		//	SET_RESULT( RESULT_OK, "" );
		ASSERT(buffer.size() > 0, RESULT_ERROR_BADSIZE, "Input buffer is empty when processing ILD", "");
		ASSERT(distance_m > 0, RESULT_ERROR_OUTOFRANGE, "Distance must be greater than zero when processing ILD", "");
		ASSERT(azimuth >= -90.0 && azimuth <= 90, RESULT_ERROR_OUTOFRANGE, "Azimuth must be between -90� and 90� when processing ILD", "");
		ASSERT(Default_ILD_AzimuthStep > 0 && Default_ILD_DistanceStep > 0, RESULT_ERROR_INVALID_PARAM, "Step values of ILD hash table are not valid", "");
		//ASSERT(ILD_Data != nullptr, RESULT_ERROR_NOTINITIALIZED, "A Hash Table should be provided through CILD::SetILD_HashTable()", "");		

		float distance_mm = distance_m * 1000.0f;

		float distSign = distance_mm > 0 ? 1 : -1;
		float azimSign = azimuth     > 0 ? 1 : -1;

		int q_distance_mm = Default_ILD_DistanceStep * (int)( (distance_mm + distSign * ((float)Default_ILD_DistanceStep) / 2) / Default_ILD_DistanceStep );
		int q_azimuth     = Default_ILD_AzimuthStep  * (int)( (    azimuth + azimSign * ((float)Default_ILD_AzimuthStep ) / 2) / Default_ILD_AzimuthStep  );

		auto itLeft  = ILD_Data.find( CILD_Key( q_distance_mm,  q_azimuth ) );
		auto itRight = ILD_Data.find( CILD_Key( q_distance_mm, -q_azimuth ) );

		if (itLeft != ILD_Data.end() ) 
		{
			FiltersLeft.GetFilter(0)->SetCoefs(   itLeft->second.coefs      );
			FiltersLeft.GetFilter(1)->SetCoefs( &(itLeft->second.coefs[5] ) );			

			FiltersLeft.Process(buffer, TARGET_SAMPLES_LEFT);
		}
		else
			SET_RESULT(RESULT_ERROR_INVALID_PARAM, "{Distance-Azimuth} key value for the left channel was not found in the ILD look up table");

		
		if (itRight != ILD_Data.end())
		{
			FiltersRight.GetFilter(0)->SetCoefs(   itRight->second.coefs       );
			FiltersRight.GetFilter(1)->SetCoefs( &(itRight->second.coefs[5]  ) );

			FiltersRight.Process(buffer, TARGET_SAMPLES_RIGHT);
		}
		else
			SET_RESULT(RESULT_ERROR_INVALID_PARAM, "{Distance-Azimuth} key value for the left channel was not found in the ILD look up table");

		//SET_RESULT(RESULT_OK, "");
	}