#ifndef _CFCONVOLVER_H_
#define _CFCONVOLVER_H_

//#include "Buffer.h"
//#include <math.h>
#include <iostream>
#include <vector>
#include "fftsg.h"
#include "Buffer.h"

#ifndef THRESHOLD
#define THRESHOLD 0.0000001f
#endif

/** \details This class implements the necessary algorithms to do the convolution, in frequency domain, between signal and a impulse response.
*/

namespace Common {
	class CFconvolver
	{

	public:

		/** \brief Default constructor
		*/
		CFconvolver();

		/** \brief Initialize the class and allocate memory.
		*   \details When this method is called, the system initializes variables and allocates memory space for the buffer.
		*	\param [in] _inputSize size of the input signal buffer (L size)
		*	\param [in] _impulseResponseSize size of the Impulse Response, which is the size of the buffer that contains the AIR or HRIR signal (P size)
		*   \throws May throw exceptions and errors to debugger
		*/
		void Setup(int _inputSize, int _impulseResponseSize);

		/** \brief Get the FFT of the input signal
		*   \details This method will extend the signal with zeros at the end until reaching the size (L+P + k) = 2^n and then make the FFT.
		*	\param [in] inputSource_time vector containing the samples of input signal in time-domain.
		*	\param [out] inputSource_frequency FFT of the input signal
		*/
		static void GetFFT(const std::vector<float>& inputSource_time, std::vector<float>& inputSource_frequency, int _IRSize);
		
		/** \brief Get the FFT of an impulse response
		*   \details This method will extend the AIR with zeros at the end until reaching the size of (L+P + k) = 2^n and then make the FFT.
		*	\param [in] IR_time impulse response in time-domain.
		*	\param [out] IR_frequency FFT of the impulse response.
		*	\pre Size of IR_time has to agree with the size indicated by the \link Setup \endlink method.
		*   \throws May throw exceptions and errors to debugger
		*/
		void GetFFT_IR(const CMonoBuffer<float>& IR_time, CMonoBuffer<float>& IR_frequency);

		/** \brief Get the FFT of the input signal
		*   \details This method will extend the signal with zeros at the end until reaching the size (L+P + k) = 2^n and then make the FFT.
		*	\param [in] inputSource_time vector containing the samples of input signal in time-domain.
		*	\param [out] inputSource_frequency FFT of the input signal
		*	\pre Size of input signal has to aagree with the size indicated by the \link Setup \endlink method.
		*   \throws May throw exceptions and errors to debugger
		*/
		void GetFFT_Input(const std::vector<float>& inputSource_time, std::vector<float>& inputSource_frequency);

		/** \brief Complex multiplication between the elements of two vectors.
		*   \details This method makes the complex multiplication of vector samples: (a+bi)(c+di) = (ac-bd)+i(ad+bc)
		*   \param [in] x Vector of samples that has real and imaginary parts interlaced. x[i] = Re[Xj], x[i+1] = Img[Xj]
		*   \param [in] h Vector of samples that has real and imaginary parts interlaced. h[i] = Re[Hj], h[i+1] = Img[Hj]
		*	\param [out] y Complex multiplication of x and h vectors
		*	\pre Both vectors (x and h) have to be the same size
		*   \throws May throw exceptions and errors to debugger
		*/
		static void ComplexMultiplication(const std::vector<float>& x, const std::vector<float>& h, std::vector<float>& y);

		/** \brief Get the IFFT of the output signal
		*   \details This method makesthe IFFT of the signal, adds the samples obtained with buffer samples in order to get output signal, and updates the buffer.
		*   \param [in] signal_frequency Vector of samples storing the output signal in frecuency domain.
		*   \param [out] signal_time Vector of samples where the IFFT of the output signal will be returned in time domain. This vector will have the size indicated in \link Setup \endlink method.
		*	\pre signal_frequency has to have the same size that the one returned by any of the GetFFT_ methods.
		*   \throws May throw exceptions and errors to debugger
		*	\sa GetFFT_Input, GetFFT_IR
		*/
		void GetIFFT_Output(const std::vector<float>& signal_frequency, std::vector<float>& signal_time, int delayDifference);
		
	private:
		// ATTRIBUTES	
		int inputSize;			//Size of the inputs buffer		
		int IRSize;				//Size of the AmbiIR buffer
		int FFTBufferSize;		//Size of the outputbuffer and zeropadding buffers	
		double normalizeCoef;		//Coef to normalize the Inverse FFT
		int ip_size;			//Size of the auxiliary array ip;
		int w_size;				//Size of the auxiliary array w;	
		std::vector<double> storageBuffer;		//To store the results of the convolution


		// METHODS 	
		//This method copies the input vector into an array and insert the imaginary part.
		static void AddImaginaryPart(const std::vector<float>& input, std::vector<double>& output);
		//This method copy the FFT-1 output array into the storage vector, remove the imaginary part and normalize the output.	
		bool CalculateOutputBuffer_OverlapAddMethod(std::vector<double>& input, std::vector<float>& outBuffer, int delay);		
		//This method rounds to zero a value that is very close to zero.
		double CloseToZero(double number);
		//This method check if a number is a power of 2
		static bool IsPowerOfTwo(int x);
		//This method Round up to the next highest power of 2 
		static int NextPowerOfTwo(int v);
	};
}
#endif
