//////////////////////////////////////////////
// CONSTRUCTOR/DESTRUCTOR

	CCore::CCore(AudioState_Struct _audioState)
		:audioState{_audioState}	{
	}

    // Default constructor calls normal one with default values
	CCore::CCore()
		:CCore{ { 44100,512 }, // AudioState with default values: 44100 Hz sampleRate  & 512 bufferSize  
			  }
	{}
//////////////////////////////////////////////
// GENERAL PURPOSE METHODS


	// Set global audio state parameters
	void CCore::SetAudioState(AudioState_Struct _audioState)
	{
        audioState = _audioState;
    }


	// Get global audio state parameters
	AudioState_Struct CCore::GetAudioState() const
	{
		// FIXME: How does this depend on Core state?
		return audioState;
	}

	//////////////////////////////////////////////

	// Get physical magnitudes
	CMagnitudes CCore::GetMagnitudes() const
	{
		return magnitudes;
	}

	//////////////////////////////////////////////

	// Set new physical magnitudes
	void CCore::SetMagnitudes(CMagnitudes _magnitudes)
	{
		magnitudes = _magnitudes;
	}

//////////////////////////////////////////////
// LISTENER METHODS

    
    // Create a new listener
    shared_ptr<CListener> CCore::CreateListener(float listenerRadius)
    {
        // Create new source and add it to this core sources
        try
        {
            shared_ptr<CListener> newSource(new CListener(this, listenerRadius));
            listeners.push_back(newSource);
            
            SET_RESULT(RESULT_OK, "Listener created succesfully");
            return newSource;
        }
        catch (std::bad_alloc& ba)
        {
            //SET_RESULT(RESULT_ERROR_BADALLOC, ba.what());
            ASSERT(false, RESULT_ERROR_BADALLOC, ba.what(), "");
            return nullptr;
        }
    }

    
    // Stateless privately called version
    void CCore::RemoveListener(shared_ptr<CListener> listener)
    {
        if (listener == nullptr)
        {
            SET_RESULT(RESULT_ERROR_INVALID_PARAM, "Pointer is NULL when attempting to remove single source DSP");
            return;
        }
        
        bool found = false;
        for( auto it = listeners.begin(); it != listeners.end(); ++it )
        {
            if (*it == listener )
            {
                listeners.erase(it);
                found = true;
                break;
            }
        }
        
        if(found)
            SET_RESULT(RESULT_OK, "Listener removed succesfully");
        else
            SET_RESULT(RESULT_ERROR_INVALID_PARAM, "Listener was not found when attempting to remove");
    }
    
    
