#include <Common/DistanceAttenuator.h>
#include <Common/Debugger.h>
//#include "Defaults.h"
#include <Common/Magnitudes.h>
#include <BinauralSpatializer/Core.h>

#define DISTANCE_ATENUATION_FILTER_DEFAULT_CUTOFF_FREQ 10000

// Defines the frequency at which audible frequencies are not attenuated in a low pass filter.
#define NO_FILTERING_CUT_OFF_FREQUENCY 20000

// Attenuation effects for far and medium distances, include low pas filtering. This is implemented with a chain of 
// biquads filters. The number of filters in this chain is defined by:
#define NUM_OF_BIQUAD_FILTERS_FOR_FAR_DISTANCE_FILTERING 1

// The default function that provides the cutoff frequency that models the distortion of far sound sources
// follows this expression:   Fc = A � 10^-B(distance - C)
// The A and B constants are obtained from the following specifications:
//   Distance = 15m --> Fc = 20Khz
//   Distance = 50m --> Fc =  5Khz
// After solving an equations system:   Cutoff Frecuency = 20000 * pow(10, -0.0172 * (distance - 15));
#define CUT_OFF_FREQUENCY_FUNCTION__COEF_A NO_FILTERING_CUT_OFF_FREQUENCY
#define CUT_OFF_FREQUENCY_FUNCTION__COEF_B 0.0172
#define CUT_OFF_FREQUENCY_FUNCTION__COEF_C DISTANCE_MODEL_THRESHOLD_FAR

//#define USE_PROFILER_DistanceAttenuator
#ifdef USE_PROFILER_DistanceAttenuator
#include <Common/Profiler.h>
CProfilerDataSet dsDAFar;
CProfilerDataSet dsDANear;
CProfilerDataSet dsDAAttenuation;
#endif

//////////////////////////////////////////////
// CONSTRUCTOR/DESTRUCTOR

CDistanceAttenuator::CDistanceAttenuator()
{
	referenceDistance = DISTANCE_MODEL_THRESHOLD_NEAR;
	//Simple distance attenuation vrbles initialization
	previousAttenuation_Channel1 = 0.0f;
	previousAttenuation_Channel2 = 0.0f;
	previousFrameAttenuation = 0.0f;
}

//////////////////////////////////////////////

void CDistanceAttenuator::Setup( int samplingRate )
{
#ifdef USE_PROFILER_DistanceAttenuator	
	PROFILER3DTI.SetAutomaticWrite(dsDAFar, "PROF_Distance_Far.txt");
	PROFILER3DTI.SetAutomaticWrite(dsDANear, "PROF_Distance_Near.txt");
	PROFILER3DTI.SetAutomaticWrite(dsDAAttenuation, "PROF_Distance_Attenuation.txt");
	PROFILER3DTI.StartRelativeSampling(dsDAFar);
	PROFILER3DTI.StartRelativeSampling(dsDANear);
	PROFILER3DTI.StartRelativeSampling(dsDAAttenuation);
#endif
	for (int c = 0; c < NUM_OF_BIQUAD_FILTERS_FOR_FAR_DISTANCE_FILTERING; c++)
	{
		shared_ptr <CBiquadFilter>f = distanceFiltersChain.AddFilter();

		f->SetSamplingFreq( samplingRate );

		f->SetCoefsFor_ButterworthFilter(NO_FILTERING_CUT_OFF_FREQUENCY);
	}
}

//////////////////////////////////////////////

void CDistanceAttenuator::ApplyDistanceEffects( CStereoBuffer<float> & buffer, float distance, 
	                                            float attenuationConstant    , float interauralAzimuthForILD, int bufferSize, int sampleRate,
	                                            bool doDistanceAttenuation   , bool doFarDistanceLPF, bool doILD,
	                                            float extraAttennuation_dB)
{
	ApplyDistanceEffects( &buffer, NULL, distance, attenuationConstant, interauralAzimuthForILD, bufferSize, sampleRate,
		                 doDistanceAttenuation, doFarDistanceLPF, doILD, extraAttennuation_dB);
}

//////////////////////////////////////////////

void CDistanceAttenuator::ApplyDistanceEffects(CMonoBuffer<float> & buffer, float distance,
											   float attenuationConstant, float interauralAzimuthForILD, int bufferSize, int sampleRate,
											   bool doDistanceAttenuation, bool doFarDistanceLPF, bool doILD,
	                                           float extraAttennuation_dB)
{
	ApplyDistanceEffects( NULL, &buffer, distance, attenuationConstant, interauralAzimuthForILD, bufferSize, sampleRate,
		                  doDistanceAttenuation, doFarDistanceLPF, doILD, extraAttennuation_dB);
}

//////////////////////////////////////////////

void CDistanceAttenuator::ApplyDistanceEffects( CStereoBuffer<float> *bufferStereo, CMonoBuffer<float> *bufferMono,float distance,
												float attenuationConstant, float interauralAzimuthForILD, int bufferSize, int sampleRate,
												bool doDistanceAttenuation, bool doFarDistanceLPF, bool doILD,
	                                            float extraAttennuation_dB)
{

	// Error check:
	//ASSERT(ownerCore != NULL, RESULT_ERROR_NULLPOINTER, "Single source DSP has no pointer to the Core", "Distance effects succesfully applied to single source");

	bool near_distance = distance <= DISTANCE_MODEL_THRESHOLD_NEAR;
	bool far_distance  = distance >= DISTANCE_MODEL_THRESHOLD_FAR;
	bool medium_distance = !far_distance && !near_distance;

	// Modeling filtering effects of the distance
	if (far_distance && doFarDistanceLPF)
	{
#ifdef USE_PROFILER_DistanceAttenuator
		PROFILER3DTI.RelativeSampleStart(dsDAFar);
#endif

		float fc = GetCutOffFreqForFarDistances(distance);

		for (int c = 0; c < NUM_OF_BIQUAD_FILTERS_FOR_FAR_DISTANCE_FILTERING; c++)
			distanceFiltersChain.GetFilter(c)->SetCoefsFor_ButterworthFilter(fc);

		if( bufferStereo != NULL)  distanceFiltersChain.Process( *bufferStereo, TARGET_SAMPLES_BOTH);
		if (bufferMono   != NULL)  distanceFiltersChain.Process( *bufferMono );

#ifdef USE_PROFILER_DistanceAttenuator
		PROFILER3DTI.RelativeSampleEnd(dsDAFar);
#endif
	}
	else if (near_distance && doILD && bufferStereo != NULL)
	{
#ifdef USE_PROFILER_DistanceAttenuator
		PROFILER3DTI.RelativeSampleStart(dsDANear);
#endif

		ILD.Process(*bufferStereo, distance, interauralAzimuthForILD);

#ifdef USE_PROFILER_DistanceAttenuator
		PROFILER3DTI.RelativeSampleEnd(dsDANear);
#endif
	}
#ifdef USE_PROFILER_DistanceAttenuator
	PROFILER3DTI.RelativeSampleStart(dsDAAttenuation);
#endif
	
	// Attenuation is computed independently
	float attenuation = GetDistanceAttenuation(doDistanceAttenuation, attenuationConstant, distance, extraAttennuation_dB);	
	////Apply attenuation directly
	//if( bufferMono   != NULL )  bufferMono  ->ApplyGain( attenuation );
	//if (bufferStereo != NULL )  bufferStereo->ApplyGain( attenuation );			
	
	////Apply attenuation gradually using Weighted Moving Average method
	//if( bufferMono   != NULL )  bufferMono  ->ApplyGainGradually(previousFrameAttenuation, attenuation, bufferSize);
	//if (bufferStereo != NULL)  bufferStereo->ApplyGainGradually(previousFrameAttenuation, attenuation, bufferSize);			
	//previousFrameAttenuation = attenuation;

	//Apply attenuation gradually using Exponential Moving Average method
	if( bufferMono   != NULL )  bufferMono	->ApplyGainExponentially(previousAttenuation_Channel1, previousAttenuation_Channel2, attenuation, bufferSize, sampleRate);
	if (bufferStereo != NULL)	bufferStereo->ApplyGainExponentially(previousAttenuation_Channel1, previousAttenuation_Channel2, attenuation, bufferSize, sampleRate);
	

#ifdef USE_PROFILER_DistanceAttenuator
	PROFILER3DTI.RelativeSampleEnd(dsDAAttenuation);
#endif
	
}

//////////////////////////////////////////////

float CDistanceAttenuator::GetDistanceAttenuation( bool doDistanceAttenuation, float attenuationConstant, 
	                                               float distance, float extraAttennuation_dB) const
{
	// Debugger:
	if (distance <= 0.0f)
	{
		SET_RESULT(RESULT_ERROR_DIVBYZERO, "Attempt to compute distance attenuation for a negative or zero distance");
		return 1.0f;
	}
	//else
	//	SET_RESULT(RESULT_OK, "Distance attenuation returned succesfully for single source");	

	// Compute attenuation factor
	float attenuationFactor = attenuationConstant / DISTANCE_ATTENUATION_REFERENCE_DB;

	// Compute gain
	if ( doDistanceAttenuation && distance > EPSILON_DISTANCE)
	{
		return powf(10.0f, extraAttennuation_dB + attenuationFactor * log10f( referenceDistance / distance ));
	}
	else
		return 1.0;
}

//////////////////////////////////////////////

// Returns the cutoff frequency of the low pass filters that is applied for long distances.
float CDistanceAttenuator::GetCutOffFreqForFarDistances(float distance)
{
	//SET_RESULT(RESULT_OK, "");

	if (distance > DISTANCE_MODEL_THRESHOLD_FAR)
	{
		// See comments in definition of constants CUT_OFF_FREQUENCY_FUNCTION__COEF_X
		float A = CUT_OFF_FREQUENCY_FUNCTION__COEF_A;
		float B = CUT_OFF_FREQUENCY_FUNCTION__COEF_B;
		float C = CUT_OFF_FREQUENCY_FUNCTION__COEF_C;

		return A * pow(10, -B * (distance - C));
	}
	else
		return NO_FILTERING_CUT_OFF_FREQUENCY;
}


void CDistanceAttenuator::ApplyGain_ExponentialMovingAverage(CStereoBuffer<float> & buffer, int gain2, float alpha) 
{
	float previousGainOdd = 0.0f;
	float gainOdd = 0.0f;
	float previousGainEven = 0.0f;
	float gainEven = 0.0f;
	int halfBufferSize = (int)(0.5f * buffer.size());
	int j = 0;
	for (int i = 0; i < halfBufferSize; i++)
	{
		gainEven = (gain2 - previousGainEven)* alpha + previousGainEven;
		buffer[j++] *= gainEven;
		previousGainEven = gainEven;
		gainOdd = (gain2 - previousGainOdd)* alpha + previousGainOdd;
		buffer[j++] *= gainOdd;
		previousGainOdd = gainOdd;
	}
}





